<!--
 * @Description:代办事项详细页面
 * @Author: zhangshujiang
 * @Date: 2019-07-22 15:11:44
 * @LastEditors: zhangshujiang
 * @LastEditTime: 2019-08-23 14:05:06
 -->
<template>
  <div>
    <headerBar :header-data="headerData" />
    <!--    &lt;!&ndash; 项目信息 begin &ndash;&gt;-->
    <div class="fixed-project">
      <div class="list">
        <h3>{{ dbxq.processName }}</h3>
        <div class="info">
          <span>
            <i class="iconfont iconlianxiren" />
            {{ dbxq.preUsers }}
          </span>
          <span>
            <i class="iconfont iconshijian" />
            {{ dbxq.showTime }}
          </span>
          <span>
            <i class="iconfont iconpingbiaobaogao" />
            {{ dbxq.taskName }}
          </span>
        </div>
      </div>
    </div>
    <!-- <div class="project-tab sp-details">
      <v-nav :list-title="title" :swiper-send-index="swiperToNav" @changeTab="clickIndex" />
      <v-swiper :swiper-index="slideIndex" @slideTab="slideSendNav" />
    </div> -->
    <van-sticky class="tabs-sticky">
      <van-tabs class="van-tabs" :active="active" swipeable animated>
        <van-tab v-for="tab in tabs" :key="tab.name" :title=" tab.name">
          <!--<jbrzbxx v-if="index.appurl == 'jbrzbxx'" :appsrvurl="index.appsrvurl" :executorid="dbxq.executorid"/>-->
          <!--<jbrlzrz v-else :zxrid="txBizid"/>-->
          <jbrldzjbx v-if="tab.appsrvurl == 'ldzjbx'" :processid="dbxq.processId" />
          <jbrlzrz v-if="tab.appsrvurl == 'lzrz'" :processid="dbxq.processId" />
          <jbrzjh v-else-if="tab.appurl == 'zjh'" :appsrvurl="tab.appsrvurl" :bizid="dbxq.bizid" :executorid="dbxq.executorid" />
          <jbrzjhpj v-else-if="tab.appurl == 'zjhPage'" :appsrvurl="tab.appsrvurl" :bi-zid="dbxq.bizid" :executorid="dbxq.executorid" @showPop="showPop" />
          <jbrxmrc v-else-if="tab.appurl == 'xmrcPage'" :appsrvurl="tab.appsrvurl" :bi-zid="dbxq.bizid" :executorid="dbxq.executorid" @showPop="showPop" />
          <jbrshbj v-else-if="tab == 2" ref="mychild" />
          <jbrzbxx v-else :appsrvurl="tab.appsrvurl" :bizid="dbxq.bizid" :executorid="dbxq.executorid" />
        </van-tab>
      </van-tabs>
    </van-sticky>
    <div v-if="lcButton.submitBtn.isShow || lcButton.hqBtn.isShow || lcButton.backBtn.isShow || lcButton.backToPreBtn.isShow" class="foot-btn fixed">
      <ul>
        <li v-if="lcButton.backToPreBtn.isShow"><van-button type="info" round plain size="large" @click="popupClick(0)">{{ lcButton.backToPreBtn.btnName }}</van-button></li>
        <li v-if="lcButton.backBtn.isShow"><van-button type="info" round plain size="large" @click="popupClick(2)">{{ lcButton.backBtn.btnName }}</van-button></li>
        <li v-if="lcButton.submitBtn.isShow"><van-button type="info" round size="large " @click="popupClick(1)">{{ lcButton.submitBtn.btnName }}</van-button></li>
        <li v-if="lcButton.hqBtn.isShow"><van-button type="info" round size="large " @click="popupClick(3,lcButton.hqBtn.btnName)">{{ lcButton.hqBtn.btnName }}</van-button></li>
      </ul>
    </div>

    <!-- 信息校验popup beign -->
    <van-dialog v-model="showCheck" title="校验信息">

      <div class="content">
        <div class="check-title">您提交的信息有如下错误:</div>
        <div class="check-message">
          <ul>
            <div v-for="(msg, index) in checkMsgs" :key="index">
              <li class="undone">{{ msg.jyx }}</li>
            </div>
          </ul>
        </div>
      </div>

    </van-dialog>
    <!-- 信息校验popup end -->
    <van-popup v-model="showDMXZPOP" round position="bottom">
      <van-picker
        show-toolbar
        :columns="colums"
        @confirm="onConfirm"
        @cancel="showDMXZPOP=false"
      />
    </van-popup>
  </div>
</template>

<script>
// import xyNavJbr from '../../../components/nav/xy-nav-jbr.vue'
import headerBar from '@/components/header/headerBar.vue'
import jbrzbxx from './dasxXxxx/jbrzbxx.vue'
import jbrlzrz from './dasxXxxx/jbrlzrz.vue'
import jbrzjh from './dasxXxxx/jbrzjh.vue'
import jbrzjhpj from './dasxXxxx/jbrzjhpj.vue'
import jbrldzjbx from './dasxXxxx/jbrldzjbx.vue'
import jbrxmrc from './dasxXxxx/jbrxmrc.vue'
import jbrshbj from './dasxXxxx/jbrshbj.vue'
import { mapState, mapMutations } from 'vuex'
import { getTaskAction, getDataTabCheckMsgsAction, getZxztAction } from '../../../store/jbr/dbsx.js'
import { Dialog } from 'vant'
import { pageRouter } from '@/common/js/mixin.js'
export default {
  name: 'DbsxXxxx',
  components: {
    jbrxmrc,
    jbrzjh,
    headerBar,
    jbrldzjbx,
    jbrzbxx,
    jbrlzrz,
    jbrshbj,
    jbrzjhpj
    // 'v-nav': jbrspnav,
    // 'v-swiper': jbrspswiper
  },
  mixins: [pageRouter],
  data() {
    return {
      colums: [],
      showDMXZPOP: false,
      // slideIndex: 0, // 点击导航发送给swiper的值
      // swiperToNav: 0, // swiper滑动下标值传给nav导航
      headerData: {
        title: '待办详情',
        leftUrl: 'back',
        leftText: ' '
      },
      dbxq: {},
      lcButton: {
        submitBtn: '',
        hqBtn: '',
        backBtn: '',
        backToPreBtn: '',
        logBtn: '',
        hasBtn: ''
      },
      tabs: [], // 标题内容
      active: 0,
      checkMsgs: [],
      showCheck: false
      // title: [] // 标题内容
    }
  },
  computed: {
    ...mapState({
      // dbitem: state => state.jbrdbsx.listItem,
      txBizid: state => state.jbrdbsx.txBizid,
      goXmxqMore: state => state.jbrdbsx.goXmxqMore,
      goPdf: state => state.jbrdbsx.goPdf,
      goImages: state => state.jbrdbsx.goImages,
      goHtml: state => state.jbrdbsx.goHtml,
      userInfo: state => state.mh.mhUserInfo
    })
  },
  created() {
    this.headerData.title = this.$route.params.title
    this.getTaskData()
  },

  methods: {
    onConfirm(value) {
      this.showDMXZPOP = false
      this.$bus.$emit('xzChange', value)
    },
    showPop(arr) {
      console.log('arr=========================================================', arr)
      this.colums = arr
      this.showDMXZPOP = true
    },
    ...mapMutations([
      'SET_GO_PDF',
      'SET_GO_IMG',
      'SET_GO_HTML',
      'jbrdbsx/SET_Refresh'
    ]),
    getTaskData() {
      console.log('executorid:', this.txBizid)
      getTaskAction({
        executorid: this.txBizid
      }).then(res => {
        console.log('task:', res)
        if (res.length === 0) {
          this.$toast.fail('暂无数据！')
        } else {
          this.dbxq = res
          const sjbqs = JSON.stringify(this.dbxq.datatabs)
          const sjbqList = JSON.parse(sjbqs)
          for (let i = 0; i < sjbqList.length; i++) {
            var datas = { 'name': sjbqList[i].name, 'appsrvurl': sjbqList[i].appsrvurl, 'appurl': sjbqList[i].appurl }
            this.tabs.push(datas)
          }
          // var lzrz = { 'name': '流转日志', 'appsrvurl': '', 'appurl': 'lzrz' }
          // this.tabs.push(lzrz)

          this.lcButton = this.dbxq.lcButton
          console.log('button========', this.lcButton)
          var hasLzrz = this.lcButton.logBtn.isShow
          if (hasLzrz) {
            var lzrz = { 'name': '流转日志', 'appsrvurl': 'lzrz', 'appurl': 'lzrz' }
            this.tabs.push(lzrz)
          }
          console.log('tabs=================================================================', this.tabs)
        }
      })
    },

    // goMore() {
    //   this.$router.push({
    //     name: 'sjbqmore'
    //   })
    // },
    popupClick(type, btnMc) {
      this.$store.dispatch('jbrdbsx/setDbsxspxz', type)
      getZxztAction({
        id: this.txBizid
      }).then(res => {
        if (res.data === 2) {
          if (type === 0) {
            if (this.dbxq.thyjbt === null || this.dbxq.thyjbt === 3 || this.dbxq.thyjbt === 2) {
              this.clickBackOneStepSP()
            } else {
              this.$router.push({
                name: 'DbsxBackOneStep'
              })
            }
          } else if (type === 1) {
            console.log('this.dbxq.executorid:', this.dbxq.executorid)
            getDataTabCheckMsgsAction({
              executorid: this.dbxq.executorid
            }).then(res => {
              console.log('datatab msgs:', res)
              if (res.length === 0) {
                if (this.dbxq.tjyjbt === 2) {
                  this.clickSubmitSP()
                } else {
                  this.$router.push({
                    name: 'DbsxSubmit'
                  })
                }
              } else {
                this.checkMsgs = res
                this.showCheck = true
              }
            })
          } else if (type === 2) {
            if (this.dbxq.thyjbt === null || this.dbxq.thyjbt === 2) {
              this.clickBackSP()
            } else {
              this.$router.push({
                name: 'DbsxBack'
              })
            }
          } else {
            this.$router.push({
              name: 'DbsxHq',
              params: { btnMc: btnMc }
            })
          }
        } else {
          Dialog.alert({
            message: res.text
          }).then(() => {
            setTimeout(() => {
              this.$router.push('/DbsxIndex')
              this.SET_Refresh('true')
            }, 2000)
          })
        }
      })
    },
    clickSubmitSP() {
      Dialog.confirm({
        message: '是否提交当前任务？'
      }).then(() => {
        this.$store.dispatch('jbrdbsx/sendSP', {
          id: this.txBizid,
          dqjgid: this.userInfo.zzid,
          dqryid: this.userInfo.id
        }).then(res => {
          this.$toast('审批成功！')
          setTimeout(() => {
            this.$router.push('/DbsxIndex')
            this.SET_Refresh('true')
          }, 2000)
        }).catch(error => {
          // this.pageStyles = 3
          this.$toast(error)
        })
      }).catch(() => {
        // on cancel
      })
    },
    clickBackSP() {
      Dialog.confirm({
        message: '是否退回当前任务？'
      }).then(() => {
        this.$store.dispatch('jbrdbsx/sendBackSP', {
          executorid: this.txBizid
          // eslint-disable-next-line no-unused-vars
        }).then(res => {
          this.$toast('退回成功！')
          setTimeout(() => {
            this.$router.push('/DbsxIndex')
            this.SET_Refresh('true')
          }, 2000)
        }).catch(error => {
          // this.loading = false
          // this.pageStyles = 3
          this.$toast(error)
        })
      }).catch(() => {
        // on cancel
      })
    },
    clickBackOneStepSP() {
      Dialog.confirm({
        message: '是否退回到上一步？'
      }).then(() => {
        this.$store.dispatch('jbrdbsx/sendBackOneStepSP', {
          id: this.txBizid
        }).then(res => {
          this.$toast('退回成功！')
          setTimeout(() => {
            this.$router.push('/DbsxIndex')
            this.SET_Refresh('true')
          }, 2000)
        }).catch(error => {
          // this.loading = false
          // this.pageStyles = 3
          this.$toast(error)
        })
      }).catch(() => {
        // on cancel
      })
    },
    // 点击导航菜单通过emit传给父亲然后父亲再传给子组件swiper组件
    clickIndex(index) {
      this.slideIndex = index
      // this.$store.commit('saveNavIndex', index)
    },
    // swiper滑动后下标通过emit传给父亲。父亲再传给子组件nav
    slideSendNav(index) {
      this.swiperToNav = index
      // this.$store.commit('saveSwiperIndex', index)
    }
  }
}
</script>

<style lang="css" scoped>
.nav-bar{box-shadow: none;}
</style>
