<template>
  <div>
    <div v-if="pageStyles == 0">
      <van-list
        ref="dida_list"
        class="common-list"
      >
        <div
          v-for="(items, index) in homedata"
          :key="index"
          class="content"
          style="margin-top:10px;"
        >
          <div class="common-main box">
            <div v-if="items.title" class="title title-page">
              <div class="title-left">
                <h2>{{ items.title }}</h2>
              </div>
            </div>
            <div
              v-for="(dxItems, dxIndexs) in items.data"
              :key="dxIndexs"
            >
              <div
                v-for="(item, index1) in dxItems"
                v-if="!items.kzzdb && items.type===2"
                :key="index1"
                class="page-list"
              >
                <div class="pagelist-left">{{ item.name }}</div>
                <div v-if="item.type === 9 || item.type === 10 || item.type === 11 || item.type === 12 " class="pagelist-right  blue">
                  <div v-if="item.files !== ''&& item.files !== null ">
                    <ul
                      v-for="(fjItem, fjIndex) in item.values"
                      :key="fjIndex"
                    >
                      <li @click="loadFiles(fjItem,item.files[fjIndex])">
                        <i class="iconfont iconxiazai" />
                        {{ fjItem }}
                      </li>
                    </ul>
                  </div>
                </div>
                <div v-else class="pagelist-right">{{ getValue(item) }}</div>
              </div>
              <div v-if="!items.kzzdb && items.type===1" class="page-list">
                <div class="pagelist-left">{{ dxItems.name }}</div>
                <div v-if="dxItems.type === 9 || dxItems.type === 10 || dxItems.type === 11 || dxItems.type === 12 " class="pagelist-right  blue">
                  <div v-if="dxItems.files !== ''&& dxItems.files !== null ">
                    <ul
                      v-for="(fjItem, fjIndex) in dxItems.values"
                      :key="fjIndex"
                    >
                      <li @click="loadFiles(fjItem,dxItems.files[fjIndex])">
                        <i class="iconfont iconxiazai" />
                        {{ fjItem }}
                      </li>
                    </ul>
                  </div>
                </div>
                <div v-else class="pagelist-right">{{ getValue(dxItems) }}</div>
              </div>

              <div v-if="items.kzzdb" class="page-list">
                <div class="pagelist-left">
                  <span
                    v-if="dxItems.bz == 1 && dxItems.sfbt === 1"
                    class="text-danger"
                  >*</span>{{ dxItems.name }}
                </div>
                <div
                  v-if="dxItems.bz === 0"
                  :class="
                    dxItems.sjlx === 12 ||
                      dxItems.sjlx === 15 ||
                      dxItems.sjlx === 20
                      ? 'pagelist-right blue'
                      : 'pagelist-right'
                  "
                >
                  <div
                    v-if="
                      dxItems.sjlx === 12 ||
                        dxItems.sjlx === 15 ||
                        dxItems.sjlx === 20
                    "
                  >
                    <div class="uploadregion">
                      <XyUploadPlus
                        :key="key"
                        ref="uploadEl"
                        :is-show="false"
                        style="width: 100%"
                        :filed="dxItems.bzdmc"
                        :filegroup="dxItems.value"
                        @getFileChangeInfo="getFileChangeInfo"
                      />
                    </div>
                  </div>
                  <div v-else>{{ dxItems.value }}</div>
                </div>

                <div v-if="dxItems.bz === 1" class="pagelist-right right">
                  <div
                    v-if="
                      dxItems.sjlx === 12 ||
                        dxItems.sjlx === 15 ||
                        dxItems.sjlx === 20
                    "
                  >
                    <div class="uploadregion">
                      <XyUploadPlus
                        :key="key"
                        ref="uploadEl"
                        :is-show="false"
                        style="width: 100%"
                        :filed="dxItems.bzdmc"
                        :filegroup="dxItems.value"
                        @getFileChangeInfo="getFileChangeInfo"
                      />
                    </div>
                  </div>
                  <div v-else>
                    <div v-if="dxItems.srfs === 2">
                      <div
                        class="right-select"
                        @click="showDMXZ(index, dxIndexs, dxItems)"
                      >
                        <span class="info">{{ getValue1(dxItems) }}</span>
                        <i class="iconfont iconarrowright" />
                      </div>
                    </div>
                    <div v-else>
                      <van-field
                        v-model="dxItems.value"
                        :placeholder="getPlaceholder(dxItems)"
                        input-align="right"
                        rows="1"
                        :disabled="dxItems.srfs === 3 ? true : false"
                        :type="getInputType(dxItems)"
                        autosize
                        :maxlength="dxItems.cd"
                        @input="inputChange(dxItems.bzdmc,dxItems.value)"
                      />
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div v-if="items.showmore && items.type===2" class="page-more" @click="goMore(items)">
            查看更多信息
            <i class="iconfont iconarrowright" />
          </div>
          <div class="foot-btn-white" style="margin: 0;">
            <van-button v-if="items.kzzdb" block plain size="large" type="info" @click="savePj(items.kzzda,items)">保存评价</van-button>
          </div>
        </div>
      </van-list>
    </div>
    <div v-if="pageStyles == 1">
      <error-page
        :page-style="pageStyles"
        @errorButton="parentReceive"
      />
    </div>
    <div class="common-space" />
  </div>
</template>

<script land="script" scoped>
import { mapState } from 'vuex'
import { getUrl } from '@/common/js/catch'
import errorPage from '@/components/result-page/index'
import XyUploadPlus from '@/components/upload/uploadPlus'
import { NativeJS } from '@/common/js/mixin.js'
export default {
  name: 'One',
  components: {
    errorPage,
    XyUploadPlus
  },
  mixins: [NativeJS],
  props: {
    appsrvurl: {
      type: null,
      required: true
    },
    executorid: {
      type: null,
      required: true
    },
    biZid: {
      type: null,
      required: true
    }
  },
  data() {
    return {
      ischeck: false,
      xzx: {}, // 下拉框所在行对象
      realArray: [],
      pageStyles: 0,
      ind: '', // 点击事件
      pageUrl: '',
      jbrzdxxPage: {
        pageStyles: 1, // 页面状态展示
        pageTitle: ''
      },
      list: {
        items: [],
        error: false, // 是否在刷新
        postUrl: '',
        reqData: '',
        dataName: '',
        noDataText: ''
      },
      showMore: false,
      homedata: [],
      pjxx: {
        pj: '',
        pjsm: ''
      },
      bizid: '',
      fileChangeInfo: {},
      positionArray: []
    }
  },
  computed: {
    ...mapState({
      dbitem: state => state.jbrdbsx.listItem
      // jbrsjbqList: state => state.jbrdbsx.jbrsjbqList
    })
  },
  created() {
    this.bizid = this.biZid
    this.getzbxxData()
    this.$bus.$on('xzChange', (val) => {
      for (let i = 0; i < this.realArray.length; i++) {
        if (this.realArray[i].label === val) {
          this.homedata[this.positionArray[0]].data[this.positionArray[1]].showvalue = val
          this.homedata[this.positionArray[0]].data[this.positionArray[1]].value = this.realArray[i].value
          this.ischeck = true
        }
      }
    })
  },
  methods: {
    onConfirm() {

    },
    onCancel() {
      this.showDMXZPOP = false
    },
    // 弹出下拉框
    showDMXZ(index, indexs, item) {
      this.xzx = item
      this.positionArray = []
      this.positionArray.push(index)
      this.positionArray.push(indexs)
      this.realArray = JSON.parse(item.gldm)
      const arr = []
      for (let i = 0; i < this.realArray.length; i++) {
        arr.push(this.realArray[i].label)
      }
      this.$emit('showPop', arr)
    },
    getFileChangeInfo(name, fileChangeInfo) {
      this.fileChangeInfo[name] = fileChangeInfo
    },
    inputChange(zdmc, zdVal) {
      this.$set(this.pjxx, zdmc, zdVal)
    },
    savePj(kzzda, items) {
      for (let i = 0; i < items.data.length; i++) {
        if (items.data[i].sfbt !== 2 && (items.data[i].value === '' || items.data[i].value === null)) {
          let str = ''
          str = items.data[i].srfs === 1 ? '请输入' : '请选择'
          this.$toast.fail(str + items.data[i].name)
          return
        } else {
          this.$set(this.pjxx, items.data[i].bzdmc, items.data[i].value)
        }
      }
      this.$store.dispatch('jbrdbsx/savePjAction', {
        bizid: this.bizid,
        pj: this.pjxx.pj,
        pjsm: this.pjxx.pjsm,
        zdyj: this.pjxx.zdyj,
        ywlb: kzzda
      }).then(res => {
        this.$toast('保存评价成功!')
      }).catch(error => {
        this.$toast(error)
      })
    },
    getValue1(item) {
      if (item.value) {
        const arr = JSON.parse(item.gldm)
        for (let i = 0; i < arr.length; i++) {
          if (Number(arr[i].value) === Number(item.value)) {
            return arr[i].label
          }
        }
      } else {
        return item.showvalue
      }
    },
    getValue(item) {
      if (item.type === 2) {
        return this.zhTime(item.value)
      }
      return item.value
    },
    loadFiles(fjItem, url) {
      console.log('fjItem=====', fjItem)
      console.log('url=====', url)
      // 打开附件 10-PDF文件 11-图片文件 12-HTML文件
      // getFiles(type, url, value) {
      const last_len = fjItem.lastIndexOf('.')
      const len = fjItem.length
      const types = fjItem.substring(last_len, len)
      console.log('types=====', types)
      if (url.indexOf('http') === -1) {
        url = getUrl('VUE_APP_FILE_API') + url
      }
      console.log('url========', url)

      //   // pathf = url.substring(last_len, len)
      if (types === '.jpg' || types === '.png' || types === '.jpeg') { // image
        // this.$emit('go-image', url)
        this.SET_GO_IMG(url)
        this.$router.push({
          name: 'GgImages',
          params: { url: url }
        })
      } else if (types === '.html') { // html
        // this.$emit('go-html', url)
        this.$router.push({
          name: 'GgHtml',
          params: { url: url }
        })
      } else {
        this.NativeDownFiles(url)
      }
      // },
    },
    zhTime(time) {
      if (time) {
        time = time + ''
      } else {
        return ''
      }
      if (time.length === 6) {
        return time.slice(0, 4) + '/' + time.slice(4, 6)
      }
      if (time.length === 8) {
        return time.slice(0, 4) + '/' + time.slice(4, 6) + '/' + time.slice(6, 8)
      }
      if (time.length === 10) {
        return time.slice(0, 4) + '/' + time.slice(4, 6) + '/' + time.slice(6, 8) + ' ' + time.slice(8, 10)
      }
      if (time.length === 12) {
        return time.slice(0, 4) + '/' + time.slice(4, 6) + '/' + time.slice(6, 8) + ' ' + time.slice(8, 10) + ':' + time.slice(10, 12)
      }
      if (time.length === 14) {
        return time.slice(0, 4) + '/' + time.slice(4, 6) + '/' + time.slice(6, 8) + ' ' + time.slice(8, 10) + ':' + time.slice(10, 12) + ':' + time.slice(12, 14)
      }
    },
    goMore(item) {
      console.log('MoreAction======', item)
      const more = []
      more.push(item.showmore)
      more.push(item.title)
      this.$router.push({
        name: 'DbsxSjbqMore',
        params: { morexx: more }
      })
    },
    getPlaceholder(item1) {
      if (item1.srfs === 3) {
        return item1.name
      } else {
        let str = ''
        if (item1.helptext) {
          str = '(' + item1.helptext + ')'
        }
        return '请输入' + item1.name + str
      }
    },
    getInputType(item1) {
      // 1=数字类型
      if (item1.sjlx === 1) {
        if (item1.xsw === 0) {
          console.info('digit====', 'digit')
          return 'digit'
        } else {
          console.info('number====', 'number')
          return 'number'
        }
      } else {
        return 'textarea'
      }
    },
    parentReceive() {
      this.getzbxxData()
    },
    changeBgc: function(index) {
      this.ind = index
    },
    MoreAction(item) {
      console.log('MoreAction======', item)
      const more = []
      more.push(item.showmore)
      more.push(item.title)
      this.$router.push({
        name: 'DbsxSjbqMore',
        params: { morexx: more }
      })
    },
    getzbxxData() {
      this.$store.dispatch('jbrdbsx/getjbrZbxxAction', {
        url: this.appsrvurl,
        bizid: this.bizid,
        executorid: this.executorid
      }).then(res => {
        this.homedata = res
        for (let i = 0; i < res.length; i++) {
          if (res[i] && res[i].bizid) {
            this.bizid = res[i].bizid
            res.splice(i, 1)
          }
        }
        console.log('res=======================================================================', res[0])
        if (this.homedata.length === 0) {
          this.pageStyles = 1
        } else {
          this.pageStyles = 0
        }
      }).catch(error => {
        // this.loading = false
        // this.pageStyles = 3
        this.pageStyles = 3
        console.log('error=======', error)
      })
      // } else {
      //   this.$toast('路径数据为空')
      // }
    }

  }

}
</script>
<style lang="css" scoped>
  /* .jrzbxx-page{height:200px}
  .common-list{height:100px} */
</style>
